<template>
  <div id="learn-insert container">
    <Alert v-if="alert" v-bind:message="alert"></Alert>
    <h1 class="page-header">
      
      <span class="pull-right">
        <router-link to="/admin" class="btn btn-success"></router-link>
      </span>
    </h1>
    <form v-if="!submmited" v-on:submit="addType">
      <div class="well">
        <p></p>
        <input type="text" placeholder="" v-model="type.name">
        <button type="submit" class="btn btn-success"></button>
      </div>
    </form>

    <div v-if="submmited">
      <h3></h3>
    </div>

    <div id="preview">
      <h3></h3>
      <p> {{type.name}}</p>
    </div>
  </div>
</template>

<script>
import Alert from "@/components/Alert";
export default {
  data() {
    return {
      type: {
        name: ""
      },
      submmited: false,
      alert: ""
    };
  },
  methods: {
    addType(e) {
      console.log("=======adding type=======");
      if (!this.type.name) {
        this.alert = "";
      } else {
        this.$http
          .post("api/type/add", this.type)
          .then(data => {
            // console.log(data);
            // console.log(this.submmited);
            this.submmited = true;
            this.$router.push({
              path: "/admin",
              query: { alert: "" }
            });
          })
          e.preventDefault();
      }
      e.preventDefault();
    }
  },
  components: { Alert }
};
</script>


<style scoped>
label {
  display: block;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea {
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor: pointer;
}

#preview {
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3 {
  margin-top: 10px;
}
</style>


